import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HelpdeskSetupComponent } from './components/helpdesk-setup/helpdesk-setup.component';
import { HelpdeskReportsComponent } from './components/helpdesk-reports/helpdesk-reports.component';
import { PrivateCategoryComponent } from './components/helpdesk-setup/private-category/private-category.component';
import { CommonCategoryComponent } from './components/helpdesk-setup/common-category/common-category.component';
import { HelpdeskTicketFilterComponent } from './components/helpdesk-ticket-filter/helpdesk-ticket-filter.component';
import { HelpdeskReportsDataComponent } from './components/helpdesk-reports/helpdesk-reports-data/helpdesk-reports-data.component';
import { HelpdeskCreateTicketComponent } from './components/helpdesk-create-ticket/helpdesk-create-ticket.component';

import { AuthGuard } from 'src/app/core/auth/guards/auth.guard';

const routes: Routes = [
	{ path: '', redirectTo: 'settings', pathMatch: 'full'},
	{ path: 'settings', component: HelpdeskSetupComponent, canActivate: [AuthGuard],
		children: [
			{ path: '', redirectTo: 'private-category', canActivate: [AuthGuard], pathMatch: 'full'},
			{ path: 'private-category', component: PrivateCategoryComponent, canActivate: [AuthGuard]},
			{ path: 'common-category', component: CommonCategoryComponent, canActivate: [AuthGuard]}, 
			{ path: '**', redirectTo: 'private-category', pathMatch: 'full'}
		]
	},
	{ path: 'create-ticket', component: HelpdeskCreateTicketComponent, canActivate: [AuthGuard]},
	{ path: 'edit-ticket/:id', component: HelpdeskCreateTicketComponent, canActivate: [AuthGuard]},
	{ path: 'unassigned',  component: HelpdeskTicketFilterComponent, canActivate: [AuthGuard]},
	{ path: 'open-tickets', component:HelpdeskTicketFilterComponent, canActivate: [AuthGuard]},
	{ path: 'closed-tickets', component:HelpdeskTicketFilterComponent, canActivate: [AuthGuard]},
	{ path: 'assigned-to-me',  component: HelpdeskTicketFilterComponent, canActivate: [AuthGuard]},
	{ path: 'all-tickets', component: HelpdeskTicketFilterComponent, canActivate: [AuthGuard]},
	{ path: 'reports', component: HelpdeskReportsComponent, canActivate: [AuthGuard]},
	{ path: 'reports/:name/:id', component: HelpdeskReportsDataComponent, canActivate: [AuthGuard] },
	{ path: '**', redirectTo: 'settings', pathMatch: 'full'}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class HelpdeskRoutingModule { }
