import { Component, OnInit, ViewEncapsulation, ViewChild, ChangeDetectorRef } from '@angular/core';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { SharedService } from 'src/app/shared/services/shared.service';
import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { ActivatedRoute, Router } from '@angular/router';
import { TicketService } from 'src/app/api/controllers/Ticket';
import * as moment from 'moment';
import { SessionService } from 'src/app/core/session/session.service';
import { StaffService } from 'src/app/api/controllers/Staff';
import { CondoAnimations } from 'src/@condo/animations';

@Component({
  selector: 'app-helpdesk-create-ticket',
  templateUrl: './helpdesk-create-ticket.component.html',
  styleUrls: ['./helpdesk-create-ticket.component.scss'],
  encapsulation  : ViewEncapsulation.None,
  animations   : CondoAnimations
})
export class HelpdeskCreateTicketComponent implements OnInit {
  @ViewChild('createHelpDeskForm') form:any;
  isTrackerSubmitted:boolean = true;
  ticket:any = {
    ticketTypeId: '',
    ticketPriorityId:'',
    staffId:null,
  };
  createdBY:string = 'self';
  ticketTypeList = [];
  ticketCategoryList = [];
  priortyTypeList = [];
  message: any = null;

  //staff
  staffsList = [];

  //block
  block:any = {
    blockId:'',
    apartmentBlockUnitId:'',
    primaryName: ''
  };
  blockunitprimeName:string;
  blockList = [];
  blockUnitList = [];

  //edit mode id
  ticketId:number;
  viewMode:string;
  statusTypeList = [];

  //comment
  ticketComment:string;
  ticketCommentList = [];
  oldData:any={};


  constructor( 
    private router: Router,
    private acticateRoute: ActivatedRoute,
    private ticketService: TicketService,
    private lookupService: LookupService,
    private sharedService: SharedService,
    private sessionService:SessionService,
    private apartmentService:ApartmentService,
    private staffService:StaffService,
    private _changeDetectorRef: ChangeDetectorRef,
  ) {
    this.acticateRoute.params.subscribe((data)=>{
      if(data.id) {
        this.ticketId = parseInt(data.id);
        this.viewMode = 'edit'
      }
    })
  }

  isAdmin(){
    return this.sessionService.isAdmin()
  }

  createdByChange() {
    this.ticket = {
      ticketTypeId: '',
      ticketPriorityId:'',
      staffId:null,
      apartmentBlockUnitUserId:null,
    };
    this.block = {
      blockId:''
    }
    this.ticketCategoryList = [];
    this.blockUnitList = [];
  }

  getCategoryList(type) {
    if(type == 'change')
    this.ticket.ticketCategoryId = "";
    //24 is common type so we passed 17
    //27 is private type so we passed 16
    let params =  {
      ApartmentId: this.sessionService.apartmentId,
      LookupTypeId: this.ticket.ticketTypeId == 24 ? 17 : 16,
    }
    this.lookupService.getLookupValueByLookupTypeId(params).subscribe((res: any) => {
      this.ticketCategoryList = res;
    }) 
  }

  getBlockUnit() {
    this.block.apartmentBlockUnitId = '';
    this.blockUnitList = [];
    let unitParams = {
      apartmentBlockId: this.block.blockId
    }
    this.apartmentService.getApartmentBlockUnitByBlockId(unitParams).subscribe((res:any) => {
      this.blockUnitList = res;
    })
  }

  getPrimaryNameForUnit() {
    let unitDetails,primaryDetails;
    this.ticket.apartmentBlockUnitUserId = '';
    this.block.primaryName = '';
    unitDetails = this.blockUnitList.filter((data)=>data.apartmentBlockUnitId == this.block.apartmentBlockUnitId);
    primaryDetails = unitDetails[0].blockUnitUserIsPrimaryContact
    if(primaryDetails.length > 0) {
      this.ticket.apartmentBlockUnitUserId = primaryDetails[0].primaryContactUserId;
      this.block.primaryName = primaryDetails[0].primaryContactName; //view Mode
    } 
  }

  getFileId1(event) {
    this.ticket.fileDetailsId1 = event;
  }

  getFileId2(event) {
    this.ticket.fileDetailsId2 = event;
  }

  createTicket() {
    this.message = null;
    if(!this.form.valid){
      window.scroll({
        top: 0,
        behavior: 'smooth'
      });
      // Show the validation message
      this.message = {
        appearance: 'outline',
        content   : "Fill the Required Fields",
        shake     : true,
        showIcon  : true,
        type      : 'error'
      };
      //Mark for check
      this._changeDetectorRef.markForCheck();
    } else {
      this.isTrackerSubmitted = false;
      let params = {
        ticket: {
          ticketId: 0,
          apartmentId : this.sessionService.apartmentId,
          ticketTypeId: this.ticket.ticketTypeId,
          title: this.ticket.title,
          description: this.ticket.description,
          resolutionDescription: null,
          ticketStatusId: 32, // Status = New (lookupvalue is 32)
          ticketCategoryId: this.ticket.ticketCategoryId,
          ticketPriorityId: this.ticket.ticketPriorityId,
          fileDetailsId1: this.ticket.fileDetailsId1,
          fileDetailsId2: this.ticket.fileDetailsId2,
          assignedTo: null,
          raisedBy: this.sessionService.userId,
          isActive: true,
          apartmentBlockUnitUserId: this.ticket.apartmentBlockUnitUserId,
          staffId :  this.ticket.staffId,
          insertedBy: this.sessionService.userId,
          insertedOn: new Date().toISOString(),
          updatedBy: null,
          updatedOn: null
        }
      }
      this.ticketService.addTicket(params).subscribe((res: any) => {
        if(res.message) {
          if(this.sessionService.isAdmin()) {
            this.router.navigate(['/ams/helpdesk/open-tickets'])
          } else {
            this.router.navigate(['/user/servicedesk/all-tickets'])
          }
          this.sharedService.openSnackBar('Ticket added successfully', 'success');
        } 
        this.isTrackerSubmitted = true;
      },(error)=>{
        this.isTrackerSubmitted = true;
        this.sharedService.openSnackBar('Server Error','error');
      });
    }
  }

  updateTicket() {
    this.message = null;
    if(!this.form.valid){
      window.scroll({
        top: 0,
        behavior: 'smooth'
      });
      // Show the validation message
      this.message = {
        appearance: 'outline',
        content   : "Fill the Required Fields",
        shake     : true,
        showIcon  : true,
        type      : 'error'
      };
      //Mark for check
      this._changeDetectorRef.markForCheck();
    } else {
      let params = {
        ticket: {
          ticketId: this.ticket.ticketId,
          apartmentId : this.ticket.apartmentId,
          ticketTypeId: this.ticket.ticketTypeId,
          title: this.ticket.title,
          description: this.ticket.description,
          resolutionDescription: this.ticket.resolutionDescription,
          raisedBy:  this.ticket.raisedBy,
          ticketStatusId: this.ticket.ticketStatusId,
          ticketCategoryId: this.ticket.ticketCategoryId,
          supervisorId: this.ticket.supervisorId,
          ticketPriorityId: this.ticket.ticketPriorityId,
          fileDetailsId1: this.ticket.fileDetailsId1,
          fileDetailsId2: this.ticket.fileDetailsId2,
          isActive: true,
          apartmentBlockUnitUserId: this.ticket.apartmentBlockUnitUserId,
          staffId :  this.ticket.staffId,
          insertedBy: this.ticket.insertedBy,
          insertedOn: this.ticket.insertedOn,
          updatedBy: this.sessionService.userId,
          updatedOn: new Date().toISOString()
        }
      }
      this.ticketService.updateTicket(params).subscribe((res: any) => {
        if (res.message) {
          this.sharedService.openSnackBar(res.message,'success');
          //comment section
          this.ticketComment = '';
          for(let key in params.ticket) {
            if(params.ticket[key] != this.oldData[key]) {
              if(key == 'ticketPriorityId')
              this.ticketComment +=`User has changed the Priority from ${this.getLabel(this.oldData[key],this.priortyTypeList)} to ${this.getLabel(params.ticket[key],this.priortyTypeList)}<br>`;
              else if(key == 'ticketTypeId')
              this.ticketComment +=`User has changed the Ticket Type from ${this.getLabel(this.oldData[key],this.ticketTypeList)} to ${this.getLabel(params.ticket[key],this.ticketTypeList)} and Category also changed<br>`;
              else if(key == 'title' )
              this.ticketComment +=`User has changed the Subject from ${this.oldData[key]} to ${(params.ticket[key])}<br>`;
              else if(key == 'description')
              this.ticketComment +=`User has changed the Description ${(params.ticket[key])}<br>`;
              else if(key == 'ticketStatusId') {
                this.ticketComment +=`User has changed the Status from ${this.getLabel(this.oldData[key],this.statusTypeList)} to ${this.getLabel(params.ticket[key],this.statusTypeList)}<br>`;
              }
             
            }
          }
          if(this.ticketComment.length > 0) 
            this.createComment('log');
        }
        else 
          this.sharedService.openSnackBar(res.errorMessage,'error');
      },(error)=>{
        this.sharedService.openSnackBar('Server Error','error');
      });
    }
  }

  getLabel(id,list) {
    let label = list.filter((data)=>data.lookupValueId == id);
    if(label.length > 0)
      return label[0].lookupValueName;
    else 
    return '';
  }

  createComment(type) {
    let commentDetails = {
      "ticketComment": {
        "ticketId": this.ticket.ticketId,
        "comments": this.ticketComment,
        "isActive": true,
        "isLog" : type == 'log' ? true : false,
        "insertedBy": this.sessionService.userId,
        "insertedOn": new Date().toISOString(),
        "updatedBy": null,
        "updatedOn": null
      }
    }
    this.ticketService.addTicketComment(commentDetails).subscribe((res: any) => {
      if(res.code == 200) {
        this.getCommentList();
        this.ticketComment = '';
        this.sharedService.openSnackBar('Comment Added Successfully','success');
      } else 
        this.sharedService.openSnackBar(res.errorMessage,'error');
    },(error)=>{
      this.sharedService.openSnackBar('Server Error','error');
    });
  }

  getCommentList() {
    let params = {
      ticketId: this.ticketId,
    };
    this.ticketService.getAllTicketCommentsByTicketId(params).subscribe((res: any) => {
      if(res.length > 0) {
        this.ticketCommentList = res.reverse(); 
      }
    })
  }

  getTimeFormat(date) {
    return moment(date).format('MMM D YYYY, ddd hh:mm a');
  }

  ngOnInit(): void {
    // Login Based View for admin or owner
    if(!this.isAdmin()) {
      this.createdBY = 'owner';
      this.ticket.apartmentBlockUnitUserId = this.sessionService.apartmentBlockUnitUserId
    }
    //Ticket Type List
    let ticketTypeParams = {
      LookupTypeId: 7
    }
    this.lookupService.getLookupValueByLookupTypeId(ticketTypeParams).subscribe((res:any) => {
      this.ticketTypeList = res;
    });
    //priorityList
    let priority = {
      LookupTypeId: 14
    }
    this.lookupService.getLookupValueByLookupTypeId(priority).subscribe((res:any) => {
      this.priortyTypeList = res
    });
    //tower
    let params = {
      apartmentId: this.sessionService.apartmentId
    }
    this.apartmentService.getApartmentBlockByApartmentId(params).subscribe((res:any) => {
      this.blockList = res;
    });
    //staff
    let staffParms = {
      apartmentId: this.sessionService.apartmentId,
      RoleTypeId: this.sessionService.roleTypeId
    }
    this.staffService.getAllStaffs(staffParms).subscribe((res:any) => {
      if(res.length) {
        this.staffsList = res;
      }
    });

    //Edit Mode
    if(this.viewMode == 'edit') {
      let ticketId = {
        ticketId: this.ticketId
      };
      this.ticketService.getTicketById(ticketId).subscribe(res=>{
        this.ticket= res[0];
        //owner or tenant edit mode
        if(this.ticket.apartmentBlockUnitUserId) {
          this.createdBY = 'owner'
          let blockUnit = {
            apartmentId:  this.sessionService.apartmentId,
            apartmentBlockUnitUserId: this.ticket.apartmentBlockUnitUserId
          }
          this.apartmentService.getApartmentBlockUnitUserByApartmentBlockUnitUserId(blockUnit).subscribe((res:any) => {
            if(res.length > 0) 
              this.blockunitprimeName = `${res[0].blockunit_Label} ${res[0].primarycontact_Name}`;
          });
        }
        //staff Edit Mode
        if(this.ticket.staffId) {
          this.createdBY = 'staff'
        }
        this.oldData = {...this.ticket};  //log info
        this.getCategoryList('edit');
      })
      let statusParams = {
        LookupTypeId: 9
      }
      this.lookupService.getLookupValueByLookupTypeId(statusParams).subscribe((res: any) => {
        this.statusTypeList = res;
      })
      this.getCommentList();
    }
  }
}
  
