import { Component, OnInit, Injector, ViewChild, HostListener, ViewEncapsulation } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { UserService } from 'src/app/api/controllers/User';
import { StaffService } from 'src/app/api/controllers/Staff';
import { TicketService } from 'src/app/api/controllers/Ticket';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { SharedService } from 'src/app/shared/services/shared.service';
import { ModalService } from 'src/app/shared/services/modal.service';
import { SessionService } from 'src/app/core/session/session.service';
import { MatDrawer } from '@angular/material/sidenav';

import * as moment from 'moment';

@Component({
  selector: 'app-helpdesk-ticket-filter',
  templateUrl: './helpdesk-ticket-filter.component.html',
  styleUrls: ['./helpdesk-ticket-filter.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class HelpdeskTicketFilterComponent implements OnInit {
  @ViewChild('matDrawer', {static: true}) matDrawer: MatDrawer;
  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;
  ticketSearch:string = "";
  ticketListData:any;
  isTicketDataLoaded:boolean = false;
  totalItems:any;
  modalService:any;
  columnData:any;
  urlType:string;
  //Filter Purpose
  filterData:any = {
    ticketStatus : '',
    staff : null,
    supervisor:null
  };
  staffsList = [];
  ticketStatusList = [];

  constructor(
    private activateRouter: ActivatedRoute,
    private router: Router,
    private injector: Injector,
    private userService:UserService,
    private staffService:StaffService,
    private ticketService: TicketService,
    private lookupService: LookupService,
    private sharedService:SharedService,
    private sessionService:SessionService
  ) {
    this.modalService = this.injector.get(ModalService);
  }

  @HostListener('window:onEditTicket', ['$event.detail'])
  onEditTicket(detail) {
    let dataRecord = this.datagrid.getrowdata(detail.rowId);
    let ticketId = dataRecord.ticketId;
    if(this.isAdmin()) 
      this.router.navigateByUrl('/ams/helpdesk/edit-ticket/' + ticketId);
    else 
      this.router.navigate(['/user/servicedesk/edit-ticket/' + ticketId]);
  }


  getPrintParams(event){
    this.datagrid.exportdata(event, 'helpdeskData');
  }

  isAdmin(){
    return this.sessionService.isAdmin()
  }

  createTicketNavigate() {
    if(this.isAdmin()) 
      this.router.navigate(['/ams/helpdesk/create-ticket']);
    else 
      this.router.navigate(['/user/servicedesk/create-ticket']);
  }
  //condo-select
  dropDownSelect(event,type) {
    if(type == 'status') 
      this.filterData.ticketStatus = event[0].lookupValueId;
    else if(type == 'staff')
      this.filterData.staff = event[0].staffId;
    else if(type == 'supervisor')
      this.filterData.supervisor = event[0].staffId;
  }

  onGlSearchFilter(event) {
    if(event != ""){
      let filtergroup = new jqx.filter();
      let filter_or_operator = 1;
      let filtervalue = event;
      let filtercondition = 'contains';
      let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
      filtergroup.operator = 'or';
      filtergroup.addfilter(filter_or_operator, filterData);
      this.datagrid.showfiltercolumnbackground(false);
      this.columnData.forEach(item => {
        if(item.datafield != 'Actions') {
          this.datagrid.addfilter(item.datafield, filtergroup, true);
        }
      })
      this.datagrid.applyfilters();
    }
    else {
      this.datagrid.clearfilters();
    }
  }

  filterApply() {
    this.goBack();
    this.isTicketDataLoaded = false;
    let params:any = {
      apartmentId: this.sessionService.apartmentId,
      ticketStatusIds:  this.filterData.ticketStatus,
      assigntoSupervisorId: this.filterData.supervisor,
      assigntoStaffId: this.filterData.staff,
      // isStaffassigned: params.isStaffassigned,
    }
    this.ticketService.getAllTicketsByApartmentId(params).subscribe((res:any) => {
      this.isTicketDataLoaded = true;
      if(res.length > 0) {
        res.sort((a,b)=>{
          return b.serialNo - a.serialNo;
        })
        let ticketInfo = {
          localdata: res,
          datatype: "array"
        }
        this.totalItems = ticketInfo.localdata.length;
        this.ticketListData = new jqx.dataAdapter(ticketInfo);
      }
    },(error)=>{
      this.isTicketDataLoaded = true;
      this.sharedService.openSnackBar('Server Error','error');
    });
  }

  clearFilter() {
    this.filterData.ticketStatus = null;
    this.filterData.staff = null;
    this.filterData.supervisor = null;
    this.getTicketByAdmin();
    this.goBack();
  }

  goBack() {
    this.matDrawer.close();
  }

  getTicketByAdmin() {
    this.isTicketDataLoaded = false;
    let params:any = {
      apartmentId: this.sessionService.apartmentId,
    }
    if(this.urlType == 'open-tickets') {
      params.ticketStatusIds = "32,33,46";
    } else if(this.urlType == 'closed-tickets') {
      params.ticketStatusIds = "34";
    } else if(this.urlType == 'unassigned') {
      params.isStaffassigned = false;
    } else if(this.urlType == 'assigned-to-me') {
      this.getTicketByAssignedUser();
      return;
    }
    this.ticketService.getAllTicketsByApartmentId(params).subscribe((res:any) => {
      this.isTicketDataLoaded = true;
      if(res.length > 0) {
        res.sort((a,b)=>{
          return b.serialNo - a.serialNo;
        })
        let ticketInfo = {
          localdata: res,
          datatype: "array"
        }
        this.totalItems = ticketInfo.localdata.length;
        this.ticketListData = new jqx.dataAdapter(ticketInfo);
      }
    },(error)=>{
      this.isTicketDataLoaded = true;
      this.sharedService.openSnackBar('Server Error','error');
    });
  }

  getTicketByAssignedUser() {
    let params = {
      apartmentId: this.sessionService.apartmentId,
      userId:  this.sessionService.userId,
    }
    this.ticketService.getAllTicketsAssignedtoUserByApartmentId(params).subscribe((res:any) => {
      this.isTicketDataLoaded = true;
      if(res.length > 0) {
        res.sort((a,b)=>{
          return b.serialNo - a.serialNo;
        })
        let ticketInfo = {
          localdata: res.reverse(),
          datatype: "array"
        }
        this.totalItems = ticketInfo.localdata.length;
        this.ticketListData = new jqx.dataAdapter(ticketInfo);
      }
    },(error)=>{
      this.isTicketDataLoaded = true;
      this.sharedService.openSnackBar('Server Error','error');
    });
  }

  getTicketsByUser() {
    let params = {
      apartmentId: this.sessionService.apartmentId,
      blockunituserId: this.sessionService.apartmentBlockUnitUserId
    }
    this.ticketService.getTicketscreatedByblockunitUserId(params).subscribe((res:any) => {
      this.isTicketDataLoaded = true;
      if(res.length > 0) {
        res.sort((a,b)=>{
          return b.serialNo - a.serialNo;
        })
        let ticketInfo = {
          localdata: res.reverse(),
          datatype: "array"
        }
        this.totalItems = ticketInfo.localdata.length;
        this.ticketListData = new jqx.dataAdapter(ticketInfo);
      }
    },(error)=>{
      this.isTicketDataLoaded = true;
      this.sharedService.openSnackBar('Server Error','error');
    });
  }

  ngOnInit() {
    this.activateRouter.url.subscribe((data)=>{
      this.urlType = data[0].path;
    })
    if(this.isAdmin()) {
      this.getTicketByAdmin();
    } else {
      this.getTicketsByUser();
    }
    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }
    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }
    this.columnData = [{
      text: 'Ticket ID',
      datafield: 'serialNo',
      width: 100,
      pinned: true,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    },{
      text: 'Type',
      datafield: 'ticketTypeId',
      cellsrenderer: (row, column, value) => {
      if(value == 24) {
        return '<div class="jqx-custom-inner-cell"><img src="assets/images/common-ticket-icon.svg"></div>'
      } else {
        return '<div class="jqx-custom-inner-cell"><img src="assets/images/private-ticket-icon.svg"></div>'
      }
      },
      minwidth: 80,
      renderer: columnrenderer
    },{
      text: 'Status',
      datafield: 'ticketStatusId',
      cellsrenderer: (row, column, value) => {
        let status,label;
        if(value == 32) {
          status = 'purple';
          label = 'New';
        } else if (value == 33) {
          status = 'orange';
          label = 'Assigned';
        } else if (value == 34) {
          status =  'green';
          label = 'Resolved';
        }  else if (value == 46) {
          status = 'red';
          label = 'On Hold';
        }
        return `<div class="jqx-custom-inner-cell">
            <div class="status-badge bg-status-${status}-700">
              <span class="font-bold text-status-${status}-900 text-uppercase">${label ? label : ''}</span>
            </div>
        </div>`
      },
      minwidth: 170,
      renderer: columnrenderer
    },{
        text: 'Category',
        datafield: 'ticketCategoryId_Label',
        cellsrenderer: cellsrenderer,
        minwidth: 170,
        renderer: columnrenderer
    },{
        text: 'Priority',
        datafield: 'ticketPriorityId',
        cellsrenderer: (row, column, value) => {
          //47==High 48==Medium 49==Low
          let priority;
        if(value == 47) {
          priority = 'bg-orange-900';
        } else if(value == 48) {
          priority = 'bg-green-900';
        } else if(value == 49) {
          priority = 'bg-purple-900';
        }
        return `<div class="simple-actions"><span class="dots ${priority}"></span></div>`
        },
        minwidth: 80,
        renderer: columnrenderer
    },{
        text: 'Subject',
        datafield: 'title',
        cellsrenderer: cellsrenderer,
        minwidth: 170,
        renderer: columnrenderer
    },{
      text: 'Supervisor',
      datafield: 'supervisorId_Label',
      cellsrenderer: cellsrenderer,
      minwidth: 150,
      renderer: columnrenderer
    },{
      text: 'Assigned to Staff',
      datafield: 'staffId_Label',
      cellsrenderer: cellsrenderer,
      minwidth: 150,
      renderer: columnrenderer
    },{
      text: 'Requested By',
      datafield: 'raisedby_Label',
      cellsrenderer: cellsrenderer,
      minwidth: 150,
      renderer: columnrenderer
    },{
      text: 'Date Requested',
      datafield: 'insertedOn',
      cellsrenderer: (row, column, value) => {
        return '<div class="jqx-custom-inner-cell">'+ moment(value).format("DD-MM-YYYY hh:mm A") +'</div>'
      },
      minwidth: 170,
      renderer: columnrenderer
    },{
      text: 'Actions',
      cellsalign: 'center',
      align: 'center',
      width: 120,
      cellsrenderer: (row: number): string => {
        return '<div class="simple-actions"><a href="javascript:void(0)" class="mr-2" onClick="editTicket('+ row +')"><i class="fa fa-pencil icon edit" title="Edit Ticket Details" aria-hidden="true"></i></a></div>'
      },
      renderer: columnrenderer
    }];

    if(this.isAdmin()) {
      //Filter Purpose => Staff
      let staffParms = {
        apartmentId: this.sessionService.apartmentId,
        RoleTypeId: this.sessionService.roleTypeId
      }
      this.staffService.getAllStaffs(staffParms).subscribe((res:any) => {
        if(res.length) {
          res.forEach((ele)=>{
            ele.customLabel = `${ele.staffName}, ${ele.roleName} - ${ele.staffCategoryName}`;
          })
          this.staffsList = res;
        }
      },error => {
        console.log(error)
      });
      //Filter Purpose => Ticket Status
      let statusParams = {
        LookupTypeId: 9
      }
      this.lookupService.getLookupValueByLookupTypeId(statusParams).subscribe((res: any) => {
        this.ticketStatusList = res;
      })
    }
  }

}

function getClassName(value){
  var name = value.split('-');
  return name[0].toLowerCase();
}
(window as any).getClassName = getClassName;

function showConfirmDeleteEvent(row){
  var event = new CustomEvent('onTicketDelete', {
    detail: {
        rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).showConfirmDeleteEvent = showConfirmDeleteEvent;


function editTicket(row){
  var event = new CustomEvent('onEditTicket', {
    detail: {
        rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).editTicket = editTicket;
